<template>
    <div class="device-portrait-content">
        <div class="header">
            <div class="button-group">
                <el-button type="primary" @click="handleAddDevice"></el-button>
            </div>
            <div class="search-content">
                <el-input v-model="searchVal" placeholder="" clearable></el-input>
                <el-button @click="fetchData" type="primary"></el-button>
            </div>
        </div>
        <!---->
        <el-table :data="tableData" border stripe>
            <el-table-column label="" type="index"></el-table-column>
            <el-table-column label="id" prop="id"></el-table-column>
            <el-table-column label="" prop="name"></el-table-column>
            <el-table-column label="" prop="totalSize">
                <template slot-scope="scope">
                    {{cc_format_number(scope.row.totalSize)}}
                </template>
            </el-table-column>
            <el-table-column label="" prop="realSize">
                <template slot-scope="scope">
                    {{cc_format_number(scope.row.realSize)}}
                </template>
            </el-table-column>
            <el-table-column label="" prop="createTime"></el-table-column>
            <el-table-column label="" prop="status">
                <template slot-scope="scope">
                    {{statusEnum[scope.row.status]}}
                </template>
            </el-table-column>
            <el-table-column label="" width="200">
                <template slot-scope="scope">
                    <el-button
                            size="mini"
                            type="primary"
                            @click="handleSeeEcharts(scope.row.id)"></el-button>
                    <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.row.id)"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <pagination :currentpage="pagination.currentPage"
                    :pagesize="pagination.pageSize"
                    :totalcount="pagination.totalCount"
                    @handle-size-change="handleSizeChange"
                    @handle-current-change="handleCurrentChange">
        </pagination>
    </div>
</template>

<script>
export default {
  name: 'List',
  components: {},
  props: ['refreshList'],
  data () {
    return {
      tableData: [],
      statusEnum: {
        0: '',
        1: '',
        2: '',
        3: '',
        4: ''
      },
      pagination: {
        pageSize: 10,
        currentPage: 1,
        totalCount: 0
      },
      searchVal: undefined
    }
  },
  watch: {
    'refreshList': function (val) {
      if (val) {
        this.fetchData()
      }
    }
  },
  methods: {
    getFilter () {
      const filter = {
        pageNum: this.pagination.currentPage,
        pageSize: this.pagination.pageSize,
        fileName: this.searchVal
      }
      return filter
    },
    fetchData () {
      const filter = this.getFilter()
      this.$service.getDevicePortraitList(filter).then(data => {
        this.tableData = data.list
        this.pagination.totalCount = data.total
      })
    },
    handleSizeChange (val) {
      this.pagination.pageSize = val
      this.fetchData()
    },
    handleCurrentChange (val) {
      this.pagination.currentPage = val
      this.fetchData()
    },
    handleSeeEcharts (id) {
      this.$emit('open-echarts', id)
    },
    handleDelete (id) {
      this.$confirm('')
        .then(() => {
          this.$service.devicePortraintDel({ id }, '').then(() => {
            this.fetchData()
          })
        })
        .catch(() => {
        })
    },
    handleAddDevice () {
      this.$emit('open-add')
    }
  },
  created () {
    this.fetchData()
  }
}
</script>

<style lang="stylus" scoped>
.device-portrait-content
    /*margin 20px*/
.header,.button-group,.search-content
    display flex
.header
    justify-content space-between
    margin 10px 0
</style>
