export function getListDimension (params) {
  return this.fetch({
    method: 'get',
    url: 'api/behavior/show',
    params
  })
}
// 
export function saveListDimension (data) {
  return this.fetch({
    method: 'post',
    url: 'api/behavior/show',
    data
  })
}
