<template>
  <div>
    <crowd-list
      v-show="isShowCrowdList"
      ref="list"
      :selectRow="selectRow"
      @goBack="goBackFirstLayer"
      @addCrowd="addCrowd"
      @editABCrowd="editABCrowd"
    ></crowd-list>
    <crowd-add
      v-if="!isShowCrowdList && !isAbTest && mode === ''"
      :crowdId="crowdId"
      :policyId="selectRow.policyId"
      :limitLaunchDisabled="effectCrowd"
      @goBackCrowdListPage="goBackCrowdListPage"
    >
    </crowd-add>
    <div v-if="!isShowCrowdList && isAbTest && mode === 'editABTest'">
      <el-tabs v-model="tabSet" type="card">
        <el-tab-pane label="" name="first">
          <crowd-add
                  :crowdId="crowdId"
                  :policyId="selectRow.policyId"
                  :limitLaunchDisabled="effectCrowd"
                  @goBackCrowdListPage="goBackCrowdListPage"
          >
          </crowd-add>
        </el-tab-pane>
        <el-tab-pane label="AB" name="second">
          <crowd-a-b-add
                  :crowd="crowd"
                  :mode="isAbTest ? 'editABTest' : ''"
                  @goBackCrowdListPage="goBackCrowdListPage"
          ></crowd-a-b-add>
        </el-tab-pane>
      </el-tabs>
    </div>
    <crowd-a-b-add
            v-if="!isShowCrowdList && mode === 'addABTest'"
            @goBackCrowdListPage="goBackCrowdListPage"
            :crowd="crowd"
    ></crowd-a-b-add>
  </div>
</template>
<script>
import CrowdAdd from './crowdAdd'
import CrowdList from './crowdList'
import CrowdABAdd from './crowdAbTest'
export default {
  data () {
    return {
      isShowCrowdList: true, // 
      crowdId: null, // ID
      isAbTest: false,
      tabSet: 'first',
      mode: '',
      crowd: undefined,
      effectCrowd: false
    }
  },
  props: ['selectRow'],
  methods: {
    goBackFirstLayer () {
      // 
      this.$emit('goBack')
    },

    addCrowd (row) {
      const tableData = this.$refs.list.tableData
      if (tableData.length > 49) {
        return this.$message.warning('')
      }
      // 
      const crowdId = row ? row.crowdId : undefined
      this.mode = ''
      this.isAbTest = false
      this.isShowCrowdList = false
      if (crowdId) {
        this.crowdId = crowdId
        // 
        this.effectCrowd = (this.selectRow.useStatus === '' && row.crowdValidStatus == 2)
      } else {
        this.crowdId = null
        this.effectCrowd = false
      }
    },
    goBackCrowdListPage (isLoadData) {
      this.isShowCrowdList = true
      if (isLoadData) this.$refs.list.loadData()
    },
    // abtest
    editABCrowd (row, mode) {
      this.crowd = row
      this.isShowCrowdList = false
      this.isAbTest = row.abMainCrowd === 1
      this.crowdId = row.crowdId
      this.mode = mode
      // ab
      this.effectCrowd = ((this.selectRow.useStatus === '' && row.apiStatus == 2) || this.isAbTest)
    }
  },
  components: {
    CrowdAdd,
    CrowdList,
    CrowdABAdd
  }
}
</script>
