<template>
  <div>
        <!-- authority -->
    <div class="TopNav">
      <div class="left">
    <el-button-group>
      <el-button
        type="primary"
        size="small"
        @click="handleAdd"
        v-permission="'sysAdministrative:user:add'"
      >
        <a class="fa fa-plus" style="color: white;"></a>
      </el-button>
      <!-- <el-button type="primary" size="small" @click="handleRefresh">
                <a class="fa fa-refresh" style="color: white;"></a> 
      </el-button>-->
      <el-button
        type="primary"
        size="small"
        @click="handleBatchDel"
        v-permission="'sysAdministrative:user:batchDel'"
      >
        <a class="fa fa-trash" style="color: white;"></a> 
      </el-button>
    </el-button-group>
      </div>
      <div class="right">
          <el-form
      :inline="true"
      :model="searchForm"
      ref="searchForm"
      @submit.native.prevent="submitForm"
    >
      <el-form-item label="" prop="userMsg">
        <el-input
          v-model="searchForm.userMsg"
          style="width: 200px"
          size="small"
          placeholder="//"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" size="small" icon="search" @click="submitForm"></el-button>
        <el-button type="primary" size="small" @click="handleReset">
          <a class="fa fa-refresh" style="color: white;"></a> 
        </el-button>
      </el-form-item>
    </el-form>
      </div>
    </div>
    <!-- talbe -->
    <el-table
      ref="myTable"
      :data="tableData"
      style="width: 100%;"
      stripe
      border
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection"></el-table-column>
      <el-table-column type="index" width="50"></el-table-column>
      <el-table-column prop="id" label="ID" width="50"></el-table-column>
      <el-table-column prop="officeName" label="" width="100"></el-table-column>
      <el-table-column prop="loginName" label="" width="200"></el-table-column>
      <el-table-column prop="name" label="" width="80"></el-table-column>
      <el-table-column prop="roleNameStr" label="" width="150"></el-table-column>
      <el-table-column prop="email" label="" width="220">
        <template slot-scope="scope">
          <el-icon name="message"></el-icon>
          <span style="margin-left: 10px">{{ scope.row.email }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="createDate" label="" width="200">
        <template slot-scope="scope">
          <el-icon name="time"></el-icon>
          <span style="margin-left: 10px">{{ scope.row.createDate }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="loginFlag" label="" width="100">
        <template slot-scope="scope">
          <el-tag type="success" close-transition="false" v-if="scope.row.loginFlag === 1">
            <a class="fa fa-unlock"></a> 
          </el-tag>
          <el-tag type="danger" close-transition="false" v-if="scope.row.loginFlag === 0">
            <a class="fa fa-lock"></a> 
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column label="" fixed="right" width="280">
        <template slot-scope="scope">
          <el-button-group>
            <!--<el-button size="small" type="success" @click="handleDetail(scope.$index, scope.row)">
                            <a class="fa fa-search" style="color: white;"></a> 
            </el-button>-->
            <el-button
              size="small"
              type="success"
              @click="handleChangetStatus(scope.$index, scope.row)"
               v-permission="'sysAdministrative:user:changeStatus'"
            >
              <a class="fa fa-edit" style="color: white;"></a> 
            </el-button>
            <el-button
              size="small"
              type="primary"
              @click="handleEdit(scope.$index, scope.row)"
               v-permission="'sysAdministrative:user:edit'"
            >
              <a class="fa fa-edit" style="color: white;"></a> 
            </el-button>
            <el-button
              size="small"
              type="info"
              @click="handleDelete(scope.$index, scope.row)"
               v-permission="'sysAdministrative:user:del'"
            >
              <a class="fa fa-trash" style="color: white;"></a> 
            </el-button>
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>
    <!-- pagination -->
    <div align="right">
            <pagination v-bind:currentpage="currentPage"
                        v-bind:pagesize="pageSize"
                        v-bind:totalcount="totalCount"
                        @handle-size-change="handleSizeChange"
                        @handle-current-change="handleCurrentChange">
            </pagination>
    </div>
    <!---->
    <el-dialog
      title=""
      :visible.sync="addFormVisible"
      v-if="addFormVisible"
      v-model="addFormVisible"
      :close-on-click-modal="false"
    >
      <el-form :model="userForm" :rules="userFormRules" ref="userForm" label-width="100px">
        <el-form-item label="" prop="name">
          <el-input size="small" v-model="userForm.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="loginName">
          <el-input size="small" v-model="userForm.loginName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input size="small" v-model="userForm.email" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input type="password" size="small" v-model="userForm.password" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleIds">
          <div style="height:100px;overflow:auto;">
          <el-checkbox-group v-model="userForm.roleIds">
            <el-checkbox
              v-for="item in userForm.roleList"
              :label="item.id"
              name="roleIds"
              :key="item.id"
            >{{ item.name }}</el-checkbox>
          </el-checkbox-group>
          </div>
        </el-form-item>
        <el-form-item label="" prop="officeId">
          <el-input style="display:none;" v-model="userForm.officeId" placeholder=""></el-input>
          <el-input size="small" readonly v-model="userForm.officeName" placeholder=""></el-input>
          <el-button size="small" type="primary" icon="search" @click="selectOffice"></el-button>
        </el-form-item>
        <el-form-item label="" prop="remarks">
          <el-input size="small" type="textarea" v-model="userForm.remarks" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="userForm.loginFlag">
            <el-radio class="radio" :label="1"></el-radio>
            <el-radio class="radio" :label="0"></el-radio>
          </el-radio-group>&nbsp;&nbsp;
          <el-checkbox label="" v-model="userForm.mailNotice"></el-checkbox>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelAdd"></el-button>
        <el-button type="primary" @click="addSubmit"></el-button>
      </div>
    </el-dialog>
     <!--  -->
     <el-dialog
        title=""
        :visible.sync="companyVisible"
        width="30%"
        class="organSelect"
        >
        <el-tree
            :data="companys"
            :props="companyProps"
            accordion
            @node-click="handleNodeClick">
        </el-tree>
        <span slot="footer" class="dialog-footer">
            <el-button @click="companySelectCancel"> </el-button>
            <el-button type="primary" @click="companySelectOk"> </el-button>
        </span>
    </el-dialog>
    <!---->
    <el-dialog
      title=""
      :visible.sync="editFormVisible"
      v-if="editFormVisible"
      v-model="editFormVisible"
      :close-on-click-modal="false"
    >
      <el-form :model="userForm" :rules="userFormRules" ref="userForm" label-width="100px">
        <el-form-item label="" prop="name">
          <el-input size="small" v-model="userForm.name" placeholder=""></el-input>
          <el-input style="display:none;" v-model="userForm.id"></el-input>
        </el-form-item>
        <el-form-item label="" prop="loginName">
          <el-input size="small" v-model="userForm.loginName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input size="small" v-model="userForm.email" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input type="password" size="small" v-model="userForm.password" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleIds">
          <div style="height:100px;overflow:auto;">
          <el-checkbox-group v-model="userForm.roleIds">
            <el-checkbox
              v-for="item in userForm.roleList"
              :label="item.id"
              name="roleIds"
              :key="item.id"
            >{{ item.name }}</el-checkbox>
          </el-checkbox-group>
          </div>
        </el-form-item>
        <el-form-item label="" prop="officeId">
          <el-input style="display:none;" v-model="userForm.officeId"></el-input>
          <el-input size="small" readonly v-model="userForm.officeName" placeholder=""></el-input>
          <el-button size="small" type="primary" icon="search" @click="selectOffice"></el-button>
        </el-form-item>
        <el-form-item label="" prop="remarks">
          <el-input size="small" type="textarea" v-model="userForm.remarks" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="userForm.loginFlag">
            <el-radio class="radio" :label="1"></el-radio>
            <el-radio class="radio" :label="0"></el-radio>
          </el-radio-group>&nbsp;&nbsp;
          <!-- <el-checkbox label="" v-model="userForm.mailNotice"></el-checkbox> -->
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelEdit"></el-button>
        <el-button type="primary" @click="editSubmit"></el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: 'personManageAA',
  data () {
    return {
      // 
      tableData: [],
      // 
      multipleSelection: [],
      // 
      criteria: {},
      // 
      searchForm: {
        userMsg: ''
      },
      // 
      addFormVisible: false, // 
      // 
      userForm: {
        id: '',
        name: '',
        loginName: '',
        email: '',
        password: '',
        officeId: '',
        remarks: '',
        loginFlag: 0,
        mailNotice: false,
        roleIds: [], // id
        // 
        officeName: '', // 
        roleList: null // 
      },
      userFormRules: {
        name: [{ required: true, message: '', trigger: 'blur' }],
        loginName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        email: [{ required: true, message: '', trigger: 'blur' }],
        // password: [
        //     {required: true, message: '', trigger: 'blur'}
        // ],
        roleIds: [
          {
            type: 'array',
            required: true,
            message: '',
            trigger: 'change'
          }
        ],
        officeName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        remarks: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      editFormVisible: false, // 
      // :pageSize
      pageSize: 10,
      // :pageNum
      currentPage: 1,
      // 
      start: 1,
      // 
      totalCount: 1,
      companys: [],
      companyProps: {
        chilidren: 'children',
        label: 'name'
      },
      companyVisible: false// 
    }
  },
  methods: {
    callback () {
      this.loadData()
    },
    // 
    loadData: function () {
      this.criteria['pageNum'] = this.currentPage
      this.criteria['pageSize'] = this.pageSize
      this.$service.get_users_json(this.criteria).then(data => {
        this.tableData = data.pageInfo.list
        this.totalCount = data.pageInfo.total
        this.userForm.roleList = data.roleList
      })
    },

    // , 1020,val=20
    handleSizeChange: function (val) {
      this.pageSize = val
      this.loadData()
    },

    // , 12,val=2
    handleCurrentChange: function (val) {
      this.currentPage = val
      this.loadData()
    },

    // ,
    submitForm: function () {
      var _this = this
      this.$refs.searchForm.validate(function (result) {
        if (result) {
          _this.criteria = _this.searchForm
          _this.loadData()
        } else {
          return false
        }
      })
    },

    // // 
    // handleRefresh: function () {
    //     window.location.href = localStorage.getItem("adminPath") + "/manage/user/index";
    // },

    // 
    handleReset: function () {
      this.$refs.searchForm.resetFields()
    },

    // 
    handleChangetStatus: function (index, row) {
      var id = row.id
      var loginFlag = row.loginFlag == 1 ? 0 : 1
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          this.$service
            .changeloginFlag({ id: id, loginFlag: loginFlag }, '')
            .then(() => {
              this.callback()
            })
        })
        .catch(() => {

        })
    },

    // 
    // handleDetail: function(index, row) {
    //   var id = row.id;
    //   // todo: 
    // },
    selectOffice () {
      this.companyVisible = true
      this.$service.get_office_tree_json().then((data) => {
        this.companys = data.officeTree
      })
    },
    handleNodeClick (data) {
    // this.userForm.company=data
      this.$appState.$set('company_data', JSON.stringify(data))
    },
    companySelectCancel () {
      this.$appState.$set('company_data', '')
      this.companyVisible = false
    },
    companySelectOk () {
      this.companyVisible = false
      const data = JSON.parse(this.$appState.$get('company_data'))
      this.userForm.officeId = data.id
      this.userForm.officeName = data.name
    },
    // 
    handleAdd: function () {
      this.addFormVisible = true
      this.userForm.id = ''
      this.userForm.name = ''
      this.userForm.loginName = ''
      this.userForm.email = ''
      this.userForm.password = ''
      this.userForm.officeId = ''
      this.userForm.remarks = ''
      this.userForm.loginFlag = 0
      this.userForm.officeName = ''
      this.userForm.roleIds = []
      this.userForm.mailNotice = false
    },

    // 
    addSubmit: function () {
      var _this = this
      this.$refs.userForm.validate(valid => {
        if (valid) {
          let userForm = JSON.stringify(_this.userForm)
          userForm = JSON.parse(userForm)
          userForm.roleIds = userForm.roleIds.join(',')
          delete userForm.officeName
          delete userForm.roleList
          this.$service.addUser(userForm, '').then(() => {
            this.callback()
            this.addFormVisible = false
          })
        } else {
          return false
        }
      })
    },

    // 
    handleEdit: function (index, row) {
      this.editFormVisible = true
      this.userForm.id = row.id
      this.userForm.name = row.name
      this.userForm.loginName = row.loginName
      this.userForm.email = row.email
      this.userForm.password = ''
      this.userForm.officeId = row.officeId
      this.userForm.remarks = row.remarks
      this.userForm.loginFlag = row.loginFlag
      this.userForm.officeName = row.officeName
      this.userForm.roleIds = row.roleIds
      this.userForm.mailNotice = false
    },

    // editSubmit
    editSubmit: function () {
      var _this = this
      this.$refs.userForm.validate(valid => {
        if (valid) {
          let userForm = JSON.stringify(_this.userForm)
          userForm = JSON.parse(userForm)
          userForm.roleIds = userForm.roleIds.join(',')
          delete userForm.officeName
          delete userForm.roleList
          this.$service.updateUser(userForm, '').then(() => {
            this.callback()
            this.editFormVisible = false
          })
        } else {
          return false
        }
      })
    },

    // 
    cancelAdd: function () {
      this.addFormVisible = false
      // this.$message({
      //     showClose: true,
      //     message: ''
      // });
    },
    cancelEdit: function () {
      this.editFormVisible = false
      this.$message({
        showClose: true,
        message: ''
      })
    },

    // 
    handleDelete: function (index, row) {
      var id = row.id
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'error'
      })
        .then(() => {
          this.$service.delUser({ id: id }, '').then(() => {
            this.callback()
          })
        })
        .catch(() => {
        })
    },

    // 
    handleSelectionChange: function (val) {
      // ,id(push)multipleSelection
      var ids = []
      for (var i = 0; i < val.length; i++) {
        ids.push(val[i].id)
      }
      this.multipleSelection = ids
    },

    // 
    handleBatchDel: function () {
      var ids = this.multipleSelection
      if (ids.length > 0) {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'error'
        })
          .then(() => {
            this.$service.batchDelUsers({ ids: ids.join(',') }, '').then(() => {
              this.callback()
            })
          })
          .catch(() => {

          })
      } else {
        this.$alert('', '', {
          confirmButtonText: '',
          type: 'warning'
        })
      }
    }
  },
  created () {
    this.loadData()
  }
  //  })
}
</script>
<style scoped lang="stylus">
    .el-checkbox-group label
       width 160px
    .el-checkbox+.el-checkbox
       margin-left: 0px;
    .organSelect >>>.is-current
        color:#3980e2;
</style>
