<template>
  <div>
    <!-- authority -->
    <div class="TopNav">
      <div class="left">
        <!-- authority -->
        <el-button-group>
          <el-button
            type="primary"
            size="small"
            @click="handleAdd"
            v-permission="'sysAdministrative:role:add'"
          >
            <a class="fa fa-plus" style="color: white;"></a>
          </el-button>
          <!-- <el-button type="primary" size="small" @click="handleRefresh">
                <a class="fa fa-refresh" style="color: white;"></a> 
          </el-button>-->
          <el-button
            type="primary"
            size="small"
            @click="handleBatchDel"
            v-permission="'sysAdministrative:role:batchDel'"
          >
            <a class="fa fa-trash" style="color: white;"></a> 
          </el-button>
        </el-button-group>
      </div>
      <div class="right">
        <!-- form search -->
        <el-form
          :inline="true"
          :model="searchForm"
          ref="searchForm"
          @submit.native.prevent="submitForm"
        >
          <el-form-item label prop="name">
            <el-input
              v-model="searchForm.name"
              style="width: 200px"
              size="small"
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" size="small" icon="search" @click="submitForm"></el-button>
            <el-button type="primary" size="small" @click="handleReset">
              <a class="fa fa-refresh" style="color: white;"></a> 
            </el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <!-- talbe -->
    <el-table
      ref="myTable"
      :data="tableData"
      style="width: 100%;"
      stripe
      border
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection"></el-table-column>
      <el-table-column type="index" width="80"></el-table-column>
      <el-table-column prop="id" label="ID" width="60"></el-table-column>
      <el-table-column prop="name" label="" width="200"></el-table-column>
      <el-table-column prop="enname" label="" width="120"></el-table-column>
      <!--<el-table-column prop="menuIds" label="" width="200"></el-table-column>-->
      <el-table-column prop="remarks" label="" width="200"></el-table-column>
      <el-table-column prop="createDate" label="" width="200">
        <template slot-scope="scope">
          <el-icon name="time"></el-icon>
          <span style="margin-left: 10px">{{ scope.row.createDate }}</span>
        </template>
      </el-table-column>
      <el-table-column prop="useable" label="" width="100">
        <template slot-scope="scope">
          <el-tag type="success" close-transition="false" v-if="scope.row.useable === 1">
            <a class="fa fa-unlock"></a> 
          </el-tag>
          <el-tag type="danger" close-transition="false" v-if="scope.row.useable === 0">
            <a class="fa fa-lock"></a> 
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column label="" fixed="right">
        <template slot-scope="scope">
          <el-button-group>
            <!--<el-button size="small" type="success" @click="handleDetail(scope.$index, scope.row)">
                            <a class="fa fa-search" style="color: white;"></a> 
            </el-button>-->
            <el-button
              size="small"
              type="success"
              @click="handleChangetStatus(scope.$index, scope.row)"
              v-permission="'sysAdministrative:role:changeStatus'"
            >
              <a class="fa fa-edit" style="color: white;"></a> 
            </el-button>
            <el-button
              size="small"
              type="primary"
              @click="handleEdit(scope.$index, scope.row)"
              v-permission="'sysAdministrative:role:edit'"
            >
              <a class="fa fa-edit" style="color: white;"></a> 
            </el-button>
            <el-button
              size="small"
              type="info"
              @click="handleDelete(scope.$index, scope.row)"
              v-permission="'sysAdministrative:role:del'"
            >
              <a class="fa fa-trash" style="color: white;"></a> 
            </el-button>
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>
    <!-- pagination -->
    <!-- pagination -->
    <div align="right">
      <pagination
        v-bind:currentpage="currentPage"
        v-bind:pagesize="pageSize"
        v-bind:totalcount="totalCount"
        @handle-size-change="handleSizeChange"
        @handle-current-change="handleCurrentChange"
      ></pagination>
    </div>
    <!---->
    <el-dialog
      title=""
      :visible.sync="addFormVisible"
      v-if="addFormVisible"
      :close-on-click-modal="false"
      size="large"
    >
      <el-row :gutter="40">
        <el-col :span="12">
          <el-form :model="roleForm" :rules="roleFormRules" ref="roleForm" label-width="100px">
            <el-form-item label="" prop="name">
              <el-input size="small" v-model="roleForm.name" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="remarks">
              <el-input size="small" type="textarea" v-model="roleForm.remarks" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-radio-group v-model="roleForm.useable">
                <el-radio class="radio" :label="1"></el-radio>
                <el-radio class="radio" :label="0"></el-radio>
              </el-radio-group>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :span="12">
          <el-form>
            <el-form-item label="" :inline="true">
              <el-input placeholder="" v-model="filterText" size="small"></el-input>
            </el-form-item>
          </el-form>
          <!-- <el-tag type="primary" style="font-size: large"></el-tag><br/><br/>
          <el-input placeholder="" v-model="filterText" size="small"></el-input>-->
          <el-tree
            :data="data2"
            show-checkbox
            node-key="id"
            ref="tree"
            :filter-node-method="filterNode"
            highlight-current
            :props="defaultProps"
          ></el-tree>
        </el-col>
      </el-row>

      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelAdd"></el-button>
        <el-button type="primary" @click="addSubmit"></el-button>
      </div>
    </el-dialog>

    <!---->
    <el-dialog
      title=""
      :visible.sync="editFormVisible"
      v-if="editFormVisible"
      v-model="editFormVisible"
      :close-on-click-modal="false"
      size="large"
    >
      <el-row :gutter="40">
        <el-col :span="12">
          <el-form :model="roleForm" :rules="roleFormRules" ref="roleForm" label-width="100px">
            <el-form-item label="" prop="name">
              <el-input size="small" v-model="roleForm.name" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="remarks">
              <el-input size="small" type="textarea" v-model="roleForm.remarks" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-radio-group v-model="roleForm.useable">
                <el-radio class="radio" :label="1"></el-radio>
                <el-radio class="radio" :label="0"></el-radio>
              </el-radio-group>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :span="12">
          <el-form>
            <el-form-item label="">
              <el-input placeholder="" v-model="filterText" size="small"></el-input>
            </el-form-item>
          </el-form>
          <!-- <el-tag type="primary" style="font-size: large"></el-tag><br/><br/>
          <el-input placeholder="" v-model="filterText"></el-input>-->
          <el-tree
            :data="data2"
            show-checkbox
            node-key="id"
            ref="editTree"
            :filter-node-method="filterNode"
            highlight-current
            :default-checked-keys="roleForm.menuIds"
            :props="defaultProps"
          ></el-tree>
        </el-col>
      </el-row>

      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelEdit"></el-button>
        <el-button type="primary" @click="editSubmit"></el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import _ from 'lodash'
export default {
  name: 'roleManageAA',
  data () {
    return {
      // 
      tableData: [],
      // 
      multipleSelection: [],
      // 
      criteria: {},
      initTree: {},
      // 
      searchForm: {
        name: ''
      },
      // 
      addFormVisible: false, // 
      // 
      roleForm: {
        id: '',
        name: '',
        remarks: '',
        useable: 0,
        menuIds: [],
        menuAllIds: []
      },
      roleFormRules: {
        name: [{ required: true, message: '', trigger: 'blur' }]
      },
      filterText: '',
      data2: null,
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      // 
      editFormVisible: false, // 
      // :pageSize
      pageSize: 10,
      // :pageNum
      currentPage: 1,
      // 
      start: 1,
      // 
      totalCount: 1
    }
  },
  watch: {
    filterText (val) {
      this.$refs.tree.filter(val)
    }
  },
  created () {
    this.loadData()
  },
  methods: {
    // callback(data, successMsg) {
    //   this.loadData();
    // },
    callback () {
      this.loadData()
    },
    // 
    loadData () {
      this.criteria['pageNum'] = this.currentPage
      this.criteria['pageSize'] = this.pageSize
      this.$service.get_roles_json(this.criteria).then(data => {
        this.tableData = data.pageInfo.list
        this.totalCount = data.pageInfo.total
        this.initTree = this.searchInit(data.menuTree)
        this.data2 = data.menuTree
      })
    },
    // , 1020,val=20
    handleSizeChange: function (val) {
      this.pageSize = val
      this.loadData()
    },
    // , 12,val=2
    handleCurrentChange: function (val) {
      this.currentPage = val
      this.loadData()
    },
    // id
    // json :
    // keys :Vueid
    getKeys (json, keys) {
      var final = []
      for (var i = 0; i < keys.length; i++) {
        final = this.searchTree(json, keys[i]).concat(final)
      }
      if (json.length == 0 || keys.length == 0) {
        return []
      } else {
        var c = _.uniq(final.concat(keys).sort())
        return c
      }
    },
    // id
    searchTree (json, id) {
      var newJson = json.concat([])
      var len = newJson.length // 
      var parentNode = []
      // id
      for (var s = 0; s < len; s++) {
        if (newJson[s].id == id) {
          if (
            newJson[s].parentNode == null ||
            newJson[s].parentNode.length == 0
          ) {
            parentNode = []
          } else {
            parentNode = newJson[s].parentNode
          }
        } else {
          continue
        }
      }
      return parentNode
    },
    searchInit (json) {
      var newJson = json.concat([])
      var len = newJson.length // 
      // var parentNode = [];
      for (var i = 0; i < len; i++) {
        var item = newJson[i]
        if (item.children && item.children.length != 0) {
          var child = item.children
          for (var j = 0; j < child.length; j++) {
            if (item.parentNode) {
              child[j].parentNode = item.parentNode.concat([item.id])
            } else {
              child[j].parentNode = [item.id]
            }
            newJson[len + j] = child[j]
          }
          len = newJson.length
        }
      }
      return newJson
    },
    // ,
    submitForm () {
      var _this = this
      this.$refs.searchForm.validate(function (result) {
        if (result) {
          _this.criteria = _this.searchForm
          _this.loadData()
        } else {
          return false
        }
      })
    },
    // // 
    // handleRefresh: function () {
    //     window.location.href = localStorage.getItem("adminPath") + "/manage/role/index";
    // },
    // 
    handleReset () {
      this.$refs.searchForm.resetFields()
    },
    // 
    handleChangetStatus (index, row) {
      var id = row.id
      var useable = row.useable == 1 ? 0 : 1
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          this.$service
            .changeRoleStatus({ id: id, useable: useable }, '')
            .then(() => {
              this.callback()
            })
        })
        .catch(() => {

        })
    },

    // 
    // handleDetail (index, row) {
    // },

    // 
    filterNode (value, data) {
      if (!value) return true
      return data.label.indexOf(value) !== -1
    },
    // 
    handleAdd () {
      this.addFormVisible = true
      this.roleForm.id = ''
      this.roleForm.name = ''
      this.roleForm.remarks = ''
      this.roleForm.useable = 0
      this.roleForm.menuIds = []
      this.roleForm.menuAllIds = []
    },
    // 
    addSubmit () {
      this.roleForm.menuIds = this.$refs.tree.getCheckedKeys()
      this.$refs.roleForm.validate(valid => {
        if (valid) {
          let roleForm = JSON.stringify(this.roleForm)
          roleForm = JSON.parse(roleForm)
          roleForm.menuAllIds = this.getKeys(this.initTree, roleForm.menuIds)
          roleForm.menuIds = roleForm.menuIds.join(',')
          roleForm.menuAllIds = roleForm.menuAllIds.join(',')
          this.$service.addRole(roleForm, '').then(() => {
            this.callback()
            this.addFormVisible = false
          })
        } else {
          return false
        }
      })
    },

    resetAdd () {
      this.$refs['roleForm'].resetFields()
      this.$refs.tree.setCheckedKeys([])
    },

    // 
    handleEdit (index, row) {
      this.editFormVisible = true
      this.roleForm.id = row.id
      this.roleForm.name = row.name
      this.roleForm.remarks = row.remarks
      this.roleForm.useable = row.useable
      this.roleForm.menuIds = row.menuIds
      var _this = this
      this.$nextTick(function () {
        _this.$refs.editTree.setCheckedKeys(_this.roleForm.menuIds)
      })
    },
    // editSubmit
    editSubmit () {
      this.roleForm.menuIds = this.$refs.editTree.getCheckedKeys()
      this.$refs.roleForm.validate(valid => {
        if (valid) {
          let roleForm = JSON.stringify(this.roleForm)
          roleForm = JSON.parse(roleForm)
          roleForm.menuAllIds = this.getKeys(this.initTree, roleForm.menuIds)
          roleForm.menuIds = roleForm.menuIds.join(',')
          roleForm.menuAllIds = roleForm.menuAllIds.join(',')
          this.$service.updateRole(roleForm, '').then(() => {
            this.callback()
            this.editFormVisible = false
          })
        } else {
          return false
        }
      })
    },

    // 
    cancelAdd () {
      this.addFormVisible = false
      this.$message({
        showClose: true,
        message: ''
      })
      this.$refs.tree.setCheckedKeys([])
      this.$refs['roleForm'].resetFields()
    },
    cancelEdit () {
      this.editFormVisible = false
      this.$message({
        showClose: true,
        message: ''
      })
    },

    // 
    handleDelete (index, row) {
      var id = row.id
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'error'
      })
        .then(() => {
          this.$service.delRole({ id: id }, '').then(() => {
            this.callback()
          })
        })
        .catch(() => {

        })
    },
    // 
    handleSelectionChange (val) {
      // ,id(push)multipleSelection
      var ids = []
      for (var i = 0; i < val.length; i++) {
        ids.push(val[i].id)
      }
      this.multipleSelection = ids
    },
    // 
    handleBatchDel () {
      var ids = this.multipleSelection
      if (ids.length > 0) {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'error'
        })
          .then(() => {
            this.$service.batchRoles({ ids: ids.join(',') }, '').then(() => {
              this.callback()
            })
          })
          .catch(() => {
            this.$message({
              showClose: true,
              message: ''
            })
          })
      } else {
        this.$alert('', '', {
          confirmButtonText: '',
          type: 'warning'
        })
      }
    }
  }
}
</script>
